/* css reset */
*{box-sizing: border-box;margin: 0;padding: 0;}
*:before,*:after{box-sizing: border-box;}
ul,ol{list-style: none;}
a{color:inherit;text-decoration: none;}
img{max-width: 100%;max-height: 100%;}

/* style */
body{background-color: #eee;}
.globalHeader{
    margin:20px;
}
@media (min-width:500px){
    .globalHeader{
        margin:60px 0 100px;
    }
}

.searchForm{
    display: flex;
    justify-content: space-between;
    max-width: 400px;
}
@media (min-width:500px){
    .searchForm{
        max-width: 400px;
        margin-left:auto;
        margin-right:auto;
    }
}
.searchForm > input{
    width: 100%;
    margin-right:10px;
    height: 40px;
    padding:0 10px;
    border:1px solid #ddd;
    border-radius: 4px;
}
.searchForm > button{
    white-space: nowrap;
    padding:0 28px;
    border:none;
    border-radius: 4px;
    background-color: #0282b9;
    color: white;
    font-size: 16px;
}

.globalMain{
    max-width: 900px;
    margin-left:auto;
    margin-right:auto;   
}

.siteList{
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
@media (min-width:500px){
    .siteList{
        margin-left: 0;
        margin-right: -25px;
        justify-content: flex-start;
}
}
.siteList > li{
    margin-bottom: 10px;
}
@media (min-width:500px){
    .siteList > li{
    margin-right: 25px;
    }   
}
.siteList .site{
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding:20px 0;
    position: relative;
    cursor: pointer; /* 手型鼠标 */
}
.siteList .site >.logo{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size:64px;
 
}
.siteList .site >.link{
    font-size:14px;
    margin-top: 4px;
}
.siteList .site >.close{
    position: absolute;
    top:5px;
    right:10px;
    display: none;
    cursor: default;
}
.siteList .site:hover >.close{
    display: block;
}
.siteList .addButton{
    width: 160px;
    background-color: white;
    border: 1px solid #ddd;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px 0;
}
.siteList .addButton .icon{ /* 设置icon大小 */ 
    width:56px;
    height:56px;
}
.siteList .addButton .text{
    font-size:14px;
    margin-top:4px;
}
.siteList .addButton .icon-wrapper{/* 设置icon布局 */ 
    width: 64px;
    height:64px;
    display: flex;
    justify-content: center;
    align-items: center;

}